import React from "react";
import { Box, Typography, Button, useTheme, useMediaQuery } from "@mui/material";
import img3 from "../images/imgv.png";
import Enquiry from "./Enquiry";

export default function About() {
  const theme = useTheme();
  const isMobile = useMediaQuery("(max-width:850px)");

  const cardData = [
    { number: "01", label: "EMPLOYEES AND EXPERIENCE" },
    { number: "02", label: "DELIVERY TO QUALITY" },
    { number: "03", label: "COMPREHENSIVE SERVICES" }
  ];

  return (
    <Box sx={{
      width: "100vw",
      minHeight: "100%",
      mt: isMobile ? 4.5 : 6,
      bgcolor: "#eef1f5",
      py: isMobile ? 0 : 0,
      pt:isMobile ? 2:4,
    }}>
      <Typography
        variant={isMobile ? "h5" : "h4"}
        sx={{
          fontWeight: 700,
          letterSpacing: 0.5,
          textAlign: "center",
          mt:isMobile ? 4:8,
          mb: isMobile ? 3 : 6,
          color: "#191e2f",
          fontSize: isMobile ? "1.8rem" : undefined
        }}
      >
        <span style={{ color: "#22ace9" }}>About</span> Us
      </Typography>
      <Box
        sx={{
          width: isMobile ? "90vw" : "88vw",
          maxWidth: 1500,
          mx: "auto",
          mb: isMobile ? 3 : 7,
          display: "flex",
          flexDirection: isMobile ? "column" : "row",
          borderRadius: 3,
          background: "#fff",
          boxShadow: "0 4px 24px rgba(45, 70, 139, 0.07)",
          overflow: "hidden",
          py: isMobile ? 1.5 : 4
        }}
      >
        {/* Left: Text */}
        <Box sx={{
          flex: 1,
          px: isMobile ? 2 : 6,
          py: isMobile ? 2.5 : 7,
          display: "flex",
          flexDirection: "column",
          justifyContent: "center",
          alignItems: isMobile ? "center" : "flex-start",
          textAlign: isMobile ? "center" : "left",
        }}>
          <Typography
            sx={{
              fontWeight: 900,
              fontSize: isMobile ? "1.175rem" : "2.2vw",
              lineHeight: 1.25,
              letterSpacing: 0.5,
              color: "#19213f",
              mb: isMobile ? 1.5 : 2,
              mt: isMobile ? 1 : 3
            }}
          >
            EMPOWERING YOUR BUSINESS WITH TAILORED{" "}
            <span style={{ color: "#22ace9" }}>SRI ESHWAR</span> LOGISTICS PTE LTD.
          </Typography>
          <Typography sx={{
            color: "#7f8da9",
            fontSize: isMobile ? "0.98rem" : 14,
            mb: isMobile ? 2 : 4,
            width: isMobile ? "95%" : "100%",
            mx: isMobile ? "auto" : 0
          }}>
            At FastGo, we believe in providing our clients with personalized and efficient logistics solutions that meet their unique needs. With years of experience
          </Typography>
          <Button
            variant="contained"
            sx={{
              backgroundColor: "#f4622a",
              color: "#fff",
              fontWeight: 600,
              borderRadius: "8px",
            
              fontSize: isMobile ? 12 : 14,
              px: isMobile ? 2.5 : 4,
              py: isMobile ? 1 : 1.5,
              boxShadow: "0 4px 14px rgba(244, 98, 42, 0.10)",
              width: isMobile ? 130 : 170,
              mx: isMobile ? "auto" : undefined,
              '&:hover': { backgroundColor: "#d94a0d" }
            }}
          >
            LEARN MORE
          </Button>
        </Box>
        {/* Right: Main Image */}
        <Box sx={{
          flex: 1,
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          bgcolor: "#fff",
          minHeight: isMobile ? 150 : 360,
          pr: isMobile ? 0 : 2,
          pt: isMobile ? 1 : 0,
        }}>
          <img
            src={img3}
            alt="Truck"
            style={{
              width: isMobile ? "82%" : "90%",
              maxWidth: isMobile ? 320 : 590,
              height: "auto",
              objectFit: "contain",
              display: "block",
              margin: "0 auto"
            }}
          />
        </Box>
      </Box>
      {/* Info Cards Row: Stack vertically in mobile */}
      <Box
        sx={{
          width: isMobile ? "96vw" : "100%",
          display: "flex",
          flexDirection: isMobile ? "column" : "row",
          justifyContent: isMobile ? "center" : "space-between",
          alignItems: "center",
          gap: isMobile ? 2 : 4,
          px: isMobile ? 1 : 2,
          py: isMobile ? 1.5 : 3,
          maxWidth: 1200,
          mx: "auto",
        }}
      >
        {cardData.map((card) => (
          <Box
            key={card.number}
            sx={{
              flex: 1,
              display: "flex",
              flexDirection: "row",
              alignItems: "center",
              bgcolor: "#101725",
              borderRadius: 3,
              minWidth: isMobile ? "90vw" : 200,
                 height: isMobile ? 240 : "120px",
  
              color: "#fff",
              boxShadow: "0 4px 18px rgba(30,36,58,0.13)",
              mx: isMobile ? "auto" : 1,
              mb: isMobile ? 1.2 : 0,
              transition: "transform 0.3s cubic-bezier(.54,.13,.45,.93), box-shadow 0.3s",
              '&:hover': {
                transform: "scale(1.08)",
                boxShadow: "0 8px 32px rgba(35,105,255,0.13)"
              },
              py:isMobile ? 3:0
            }}
          >
            <Typography
              sx={{
                fontWeight: 900,
                fontSize: isMobile ? "1.5rem" : "2rem",
                letterSpacing: 2,
                mr: isMobile ? 1 : 2,
                ml: isMobile ? 2 : 4
              }}
            >
              {card.number}
            </Typography>
            <Box
              sx={{
                borderLeft: "2px dotted #fff",
                height: isMobile ? 22 : 34,
                mx: isMobile ? 1 : 2
              }}
            />
            <Typography
              sx={{
                fontWeight: 700,
                fontSize: isMobile ? ".95rem" : "1rem",
                letterSpacing: 1.1,
                lineHeight: 1.2,
                ml: isMobile ? 1 : 2
              }}
            >
              {card.label}
            </Typography>
          </Box>
        ))}
      </Box>
   <Enquiry/>
    </Box>
  );
}
