import React from "react";
import { Box, TextField, Button, Typography, Paper } from "@mui/material";
import { motion } from "framer-motion";
import Contact from "./Contact";

export default function Enquiry() {
  const fadeUp = {
    hidden: { opacity: 0, y: 30 },
    visible: (i) => ({
      opacity: 1,
      y: 0,
      transition: { delay: i * 0.15, duration: 0.6, ease: "easeOut" },
    }),
  };

  const fieldStyle = {
    backgroundColor: "#ffffff",
    borderRadius: "6px",
    "& .MuiOutlinedInput-root": {
      "& fieldset": { borderColor: "#dcdcdc" },
      "&:hover fieldset": { borderColor: "#3fa3db" },
      "&.Mui-focused fieldset": { borderColor: "#3fa3db", borderWidth: 2 },
    },
  };

  const labelStyle = {
    mb: 1,
    fontWeight: 500,
    fontSize: "0.95rem",
    color: "#1a1a1a",
  };

  return (
    <Box sx={{mt:{xs:2,md:8}}}>
      <Box
        sx={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          minHeight: "100vh",
          bgcolor: "#edf0f3",
          px: 2,
        }}
      >
        <motion.div
          initial={{ opacity: 0, scale: 0.95 }}
          animate={{ opacity: 1, scale: 1 }}
          transition={{ duration: 0.5 }}
        >
          <Paper
            elevation={0}
            sx={{
              p: 4,
              borderRadius: 2,
              width: { xs: "auto", sm: "1050px" },
              background: "#edf0f3",
            }}
          >
            {/* Title */}
            <motion.div
              initial={{ opacity: 0, y: -30 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.7 }}
            >
              <Typography
                variant="h4"
                sx={{
                  fontWeight: 700,
                  mb: 4,
                  textAlign: "center",
                  color: "#3fa3db",
                }}
              >
                Enquiry
              </Typography>
            </motion.div>

            {/* Name & Age Row */}
            <motion.div
              variants={fadeUp}
              initial="hidden"
              animate="visible"
              custom={0}
            >
              <Box
                sx={{
                  display: "flex",
                  flexDirection: { xs: "column", md: "row" },
                  gap: 2,
                }}
              >
                <Box sx={{ flex: 1 }}>
                  <Typography sx={labelStyle}>Enter Your Name</Typography>
                  <TextField
                    placeholder="Your Name"
                    variant="outlined"
                    fullWidth
                    sx={fieldStyle}
                  />
                </Box>

                <Box sx={{ flex: 1 }}>
                  <Typography sx={labelStyle}>Enter Your Age</Typography>
                  <TextField
                    placeholder="Your Age"
                    variant="outlined"
                    fullWidth
                    sx={fieldStyle}
                  />
                </Box>
              </Box>
            </motion.div>

            {/* Email & Phone Row */}
            <motion.div
              variants={fadeUp}
              initial="hidden"
              animate="visible"
              custom={1}
            >
              <Box
                sx={{
                  display: "flex",
                  flexDirection: { xs: "column", md: "row" },
                  gap: 2,
                  mt: 5,
                }}
              >
                <Box sx={{ flex: 1 }}>
                  <Typography sx={labelStyle}>Enter Your Email</Typography>
                  <TextField
                    placeholder="Your Email"
                    variant="outlined"
                    fullWidth
                    sx={fieldStyle}
                  />
                </Box>

                <Box sx={{ flex: 1 }}>
                  <Typography sx={labelStyle}>Enter Your Phone</Typography>
                  <TextField
                    placeholder="Your Phone"
                    variant="outlined"
                    fullWidth
                    sx={fieldStyle}
                  />
                </Box>
              </Box>
            </motion.div>

            {/* Message */}
            <motion.div
              variants={fadeUp}
              initial="hidden"
              animate="visible"
              custom={4}
            >
              <Box sx={{ mt: 3 }}>
                <Typography sx={labelStyle}>Enter Your Message</Typography>
                <TextField
                  placeholder="Type your message here..."
                  variant="outlined"
                  fullWidth
                  multiline
                  rows={4}
                  sx={fieldStyle}
                />
              </Box>
            </motion.div>

            {/* Submit Button */}
            <motion.div
              initial={{ opacity: 0, y: 30 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.6, delay: 1 }}
            >
              <Box
                sx={{
                  display: "flex",
                  justifyContent: { xs: "center", md: "flex-start" },
                }}
              >
                <Button
                  variant="contained"
                  sx={{
                    width: { xs: "200px", md: "500px" },
                    mt: 4,
                    py: 1.5,
                    ml:{xs:0,md:35},
                    fontSize: "1rem",
                    fontWeight: "bold",
                    textTransform: "uppercase",
                    borderRadius: "4px",
                    background: "#000000",
                    "&:hover": { background: "#333333" },
                  }}
                >
                  Submit
                </Button>
              </Box>
            </motion.div>
          </Paper>
        </motion.div>
      </Box>
      <Contact />
    </Box>
  );
}
