import React from "react";
import {
  Box,
  Grid,
  Paper,
  Typography,
  Stack,
  TextField,
  Button,
  IconButton,
  Link
} from "@mui/material";
import {
  Email as EmailIcon,
  Home as HomeIcon,
  Phone as PhoneIcon,
  LocationOn as LocationOnIcon,
  Instagram as InstagramIcon,
  LinkedIn as LinkedInIcon,
  Facebook as FacebookIcon,
  Twitter as TwitterIcon,
} from "@mui/icons-material";

export default function Contact() {
  return (
    <Box sx={{ bgcolor: "#F5F5F5", minHeight: "100%",mt:{xs:12,md:8}}}>
      <Typography variant="h3" align="center" sx={{ fontWeight: 700,py:{xs:0,md:2}}}>
        Contact <span style={{ color: "#49a0e6" }}>Us</span>
      </Typography>
      {/* Top Contact Card */}
 
      {/* Title */}
      
      {/* Main two-column layout */}
      <Grid container spacing={2} justifyContent="center" sx={{ maxWidth: 1100,mt:3,}}>
        {/* Left: contact info, NO Paper */}
        <Grid item xs={12} md={6}  mt={8} sx={{ml:{xs:12}}}>
          <Box sx={{ display: "flex", flexDirection: "column", gap: 3 }}>
            <Stack direction="row" alignItems="center" gap={2}>
              <EmailIcon sx={{ color: "#151515" }} />
              <Typography sx={{ fontWeight: 500 }}>example@sriesywer.com</Typography>
            </Stack>
            <Stack direction="row" alignItems="center" gap={2}>
              <HomeIcon sx={{ color: "#151515" }} />
              <Typography sx={{ fontWeight: 500 }}>
                Door No:L-13, T-1, 4th floor, Archana pragathi apartment,
              </Typography>
            </Stack>
            <Stack direction="row" alignItems="center" gap={2}>
              <PhoneIcon sx={{ color: "#151515" }} />
              <Typography sx={{ fontWeight: 500 }}>+44 123 654 7890</Typography>
            </Stack>
            <Stack direction="row" alignItems="center" gap={1}>
              <LocationOnIcon sx={{ color: "#151515" }} />
              <Typography fontWeight={700}>Company Live Location</Typography>
            </Stack>
            {/* Clickable box for location */}
<Box sx={{ mt: 1,ml:4}}>
  <a 
    href="https://www.google.com/maps/place/Singapore" 
    target="_blank" 
    rel="noopener noreferrer"
    style={{ display: "inline-block", cursor: "pointer", textDecoration: "none" }}
  >
    <img
      src="https://www.shutterstock.com/shutterstock/videos/1100870599/thumb/10.jpg?ip=x480"
      alt="Singapore Map"
      style={{ width: 200, height: 90, borderRadius: 8 }}
    />
  </a>
</Box>

          </Box>
        </Grid>

        {/* Right: Contact form */}
      <Grid item xs={12}  sx={{ ml:{ xs:"2px",md:"10px"}}}>
  <Box
    component="form"
    sx={{
      display: "flex",
      flexDirection: { xs: "column", md: "column" }, // Both mobile and desktop stack vertically for most forms. 
      gap: 2,
      justifyContent: "center",
      alignItems: { xs: "stretch", md: "flex-start" }, // Stretch on mobile, left align on desktop
      maxWidth: 690,
      mx: { xs: 0, md: 8 }, // No horizontal margin on mobile, margin on desktop
      width: { xs: "100%", md: "100%" }, // Full on mobile, centered on desktop
      mt: 4,
      mb: 4,
      p:{xs:2,md:4}
    
    }}
    noValidate
    autoComplete="off"
  >
    <TextField label="Name" required sx={{ width: '100%', }}  />
    <TextField label="Email" required sx={{ width: '100%' }} />
    <TextField label="Mobile No" required sx={{ width: '100%' }} />
    <TextField
      label="Message"
      required
      multiline
      rows={4}
      sx={{ width: '100%' }}
    />
    <Button
      type="submit"
      variant="contained"
      sx={{
        bgcolor: "#232323",
        color: "#fff",
        fontWeight: 600,
        py: 1.3,
        fontSize: "1rem",
        mt: 1,
        boxShadow: 2,
        px:5,
        mx:"auto",
        width: { xs: "100%", md: 200 }, // Button full width on mobile, fixed on desktop
        "&:hover": { bgcolor: "#35424a" }
      }}
      fullWidth={true}
    >
      SUBMIT
    </Button>
  </Box>
</Grid>
      </Grid>
  
      {/* Footer Section */}
<Box
  sx={{
    
    bgcolor: "#F5F5F5",
    px: { xs: 2, md: 5 },
    py: { xs: 4, md: 6 },
  
    width:{md:"auto",xs:"400px"},
    height:"100%",
    borderTop: "1px solid #f0f0f2",
    boxShadow:3,
    
    
    
  }}
>
  
  <Grid
    container
    spacing={{ xs: 7, md: 15}}
    alignItems="flex-start"
    justifyContent="center"
    
   
    sx={{
      maxWidth: "100%",
      mx: "auto",
      height:"100%",
      textAlign: { xs: "center", md: "left" },
     
    }}
  >
    {/* Company info */}
    <Grid item xs={12} md={4} sx={{mt:{xs:0,md:2}}} px={6} >
      <Typography variant="h6" fontWeight={600} sx={{ mb: 2 }}>
        SRI-ESWAR LOGISTICS <br /> PTE LIMITED
      </Typography>
      <Typography sx={{ fontSize: 14, color: "#222", mb: 2 }}>
        Srieswar@gmail.com <br />
        +44 123 654 7890
      </Typography>
      <Stack
        direction="row"
        spacing={1}
        justifyContent={{ xs: "center", md: "flex-start" }}
        sx={{ mt: 2 }}
      >
        <IconButton size="small" sx={{ bgcolor: "#fff", boxShadow: 1 }}>
          <InstagramIcon sx={{ color: "#8a2981" }} />
        </IconButton>
        <IconButton size="small" sx={{ bgcolor: "#fff", boxShadow: 1 }}>
          <LinkedInIcon sx={{ color: "#0a66c2" }} />
        </IconButton>
        <IconButton size="small" sx={{ bgcolor: "#fff", boxShadow: 1 }}>
          <FacebookIcon sx={{ color: "#3b5998" }} />
        </IconButton>
        <IconButton size="small" sx={{ bgcolor: "#fff", boxShadow: 1 }}>
          <TwitterIcon sx={{ color: "#1da1f2" }} />
        </IconButton>
      </Stack>
    </Grid>

    {/* Blog */}
    <Grid item xs={12} sm={6} md={2}   sx={{mt:{xs:2,md:3}}}>
      <Typography fontWeight={600} sx={{ mb: 0.5 }}>
        Blog
      </Typography>
      <Box mt={1}>
        {["Company", "Career", "Mobile", "How it works"].map((text, i) => (
          <Link
            key={i}
            href="#"
            underline="none"
            color="#555"
            sx={{ display: "block", mb: 0.5, fontSize: 14 }}
          >
            {text}
          </Link>
        ))}
      </Box>
    </Grid>

    {/* About */}
    <Grid item xs={12} sm={6} md={2}   sx={{mt:{xs:2,md:3}}}>
      <Typography fontWeight={600} sx={{ mb: 0.5 }}>
        About
      </Typography>
      <Box mt={1}>
        {["Contacts", "About us", "Our Team", "Terms of service"].map(
          (text, i) => (
            <Link
              key={i}
              href="#"
              underline="none"
              color="#555"
              sx={{ display: "block", mb: 0.5, fontSize: 14 }}
            >
              {text}
            </Link>
          )
        )}
      </Box>
    </Grid>

    {/* Client 1 */}
    <Grid item xs={12} sm={6} md={2}   sx={{mt:{xs:1,md:3,}}}>
      <Typography fontWeight={600} sx={{ mb: 0.5,mr:{xs:4,md:0} }}>
       Our Client <br/> Website
      </Typography>
      <Box mt={1} sx={{mr:{xs:4,md:0}}}>
        {["Ninjs", "Ninjs 1", "Ninjs 2", "Ninjs 3", "Ninjs 5"].map((text, i) => (
          <Link
            key={i}
            href="#"
            underline="none"
            color="#555"
            sx={{ display: "block", mb: 0.5, fontSize: 14 }}
          >
            {text}
          </Link>
        ))}
      </Box>
    </Grid>

    {/* Client 2 */}
    <Grid item xs={12} sm={6} md={2}    sx={{mt:{xs:2,md:3,},mr:{xs:2,md:0}}}>
      <Typography fontWeight={600} sx={{ mb: 0.5 }}>
        Client
      </Typography>
      <Box mt={1}>
        {["Ninjs", "Ninjs 1", "Ninjs 2", "Ninjs 3", "Ninjs 5"].map((text, i) => (
          <Link
            key={i}
            href="#"
            underline="none"
            color="#555"
            sx={{ display: "block", mb: 0.5, fontSize: 14 }}
          >
            {text}
          </Link>
        ))}
      </Box>
    </Grid>
  </Grid>
</Box>
    </Box>
  );
}
